/* Applica box-sizing: border-box a tutti gli elementi */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;  
}
  
.main_div {
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

@media screen and (max-width: 1196px) {
  .headerContainer {
      flex-direction: column;
      gap: 10px;
  }
}



